'use client';

import { useEffect } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { Clock, CheckCircle, AlertCircle, Phone, ArrowRight } from 'lucide-react';
import { useAuth } from '@/components/providers/AuthProvider';
import { AppButton } from '@/components/ui/app-button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';

export default function Activation() {
    const { user, authStatus, profileStatus } = useAuth();
    const router = useRouter();

    useEffect(() => {
        if (authStatus === 'anonymous') {
            router.replace('/entrar');
        } else if (user?.status === 'approved') {
            router.replace(user.role === 'admin' ? '/admin' : '/vendedor');
        }
    }, [user, authStatus, router]);

    const isPending = user?.status === 'pending';
    const isRejected = user?.status === 'rejected';
    const isBlocked = user?.status === 'blocked' || user?.status === 'suspended';

    return (
        <div className="flex min-h-[80vh] items-center justify-center bg-muted/50 px-4 py-10">
            <div className="w-full min-w-0 max-w-md">
                <Card className="shadow-lg">
                    <CardHeader className="text-center">
                        <div className="mx-auto mb-4 flex h-12 w-12 items-center justify-center rounded-full bg-primary/10">
                            {isPending && <Clock className="h-6 w-6 text-primary" />}
                            {isRejected && <AlertCircle className="h-6 w-6 text-destructive" />}
                            {isBlocked && <AlertCircle className="h-6 w-6 text-destructive" />}
                            {!isPending && !isRejected && !isBlocked && <CheckCircle className="h-6 w-6 text-primary" />}
                        </div>
                        <CardTitle className="text-xl">
                            {isPending && 'Conta em análise'}
                            {isRejected && 'Conta não aprovada'}
                            {isBlocked && 'Conta bloqueada'}
                            {!isPending && !isRejected && !isBlocked && 'Verificando conta...'}
                        </CardTitle>
                        <CardDescription>
                            {isPending && 'O seu perfil está sendo revisado pela nossa equipa.'}
                            {isRejected && 'Infelizmente o seu perfil não preenche os requisitos.'}
                            {isBlocked && 'A sua conta foi suspensa por violar os nossos termos.'}
                        </CardDescription>
                    </CardHeader>
                    <CardContent className="space-y-4 text-center">
                        {isPending && (
                            <p className="text-sm text-muted-foreground">
                                Este processo costuma levar menos de 24 horas. Receberá um email assim que for aprovado.
                            </p>
                        )}
                        {isRejected && (
                            <p className="text-sm text-muted-foreground">
                                Pode entrar em contacto com o suporte para entender o motivo ou tentar um novo cadastro.
                            </p>
                        )}
                        <div className="rounded-lg bg-muted p-4">
                            <p className="text-xs font-medium uppercase tracking-wider text-muted-foreground">Seu Status</p>
                            <p className="mt-1 text-sm font-semibold text-foreground">
                                {user?.status === 'pending' && 'Pendente'}
                                {user?.status === 'rejected' && 'Rejeitado'}
                                {user?.status === 'blocked' && 'Bloqueado'}
                                {user?.status === 'suspended' && 'Suspenso'}
                            </p>
                        </div>
                    </CardContent>
                    <CardFooter className="flex flex-col gap-2 border-t pt-6">
                        <AppButton variant="outline" className="w-full gap-2" asChild>
                            <a href="https://wa.me/258840000000" target="_blank" rel="noopener noreferrer">
                                <Phone className="h-4 w-4" />
                                Contactar Suporte
                            </a>
                        </AppButton>
                        <AppButton variant="ghost" className="w-full" asChild>
                            <Link href="/">Voltar ao Início</Link>
                        </AppButton>
                    </CardFooter>
                </Card>
            </div>
        </div>
    );
}
